define('admin-dashboard/controllers/plugins/users/user', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend({
    submitMessage: '',
    newPassword: '',
    actions: {
      updatePassword: function updatePassword(password) {
        var controller = this;
        window.hoodieAdmin.user.update('user', this.model.proper_name, {
          password: password
        }).done(function () {
          controller.setProperties({
            'submitMessage': 'Successfully updated password.',
            'newPassword': ''
          });
        }).fail(function (error) {
          controller.setProperties({
            'submitMessage': 'Error: ' + error.status + ' - ' + error.responseText,
            'newPassword': ''
          });
        });
      }
    }
  });

});